body {
  margin: 0;
  padding: 10px;
  font-family: sans-serif;
  background-image: url('https://i.gyazo.com/8d02888f1921e2715a971bc2eb7b84c3.jpg');
  -webkit-transform: scale(0.95);
      -ms-transform: scale(0.95);
          transform: scale(0.95);
}

.appdiv {
  color: white;
  background-color: rgb(122, 61, 61);
  border: 2px double white;
  border-radius: 10px;
  padding: 10px;
}

#torchimg {
  height: 350px;
  margin-top: 100px;
}

#app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#newdiv {
  font-size: 20px;
}

.name {
  background-color: rgb(255, 244, 244);
}

.mainbtn {
  padding: 5px;
  border: 1px solid white;
  border-radius: 7px;
  background-color: rgb(96, 35, 35);
  color: white;
  -webkit-transform: scale(1.275);
      -ms-transform: scale(1.275);
          transform: scale(1.275);
}

.mainbtn:hover {
  -webkit-transform: scale(1.35);
      -ms-transform: scale(1.35);
          transform: scale(1.35);
  cursor: pointer;
}

#homediv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.vip {
  color: rgb(255, 211, 211);
}

#questbtn {
  border: 2px solid white;
  border-radius: 10px;
  background-color: rgb(68, 32, 32);
  color: white;
  font-size: 20px;
  padding: 20px;
}

#questbtn:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

#questbtn:active {
  -webkit-transform: scale(1.135);
      -ms-transform: scale(1.135);
          transform: scale(1.135);
}

.hirebtn {
  padding: 5px;
  border: 1px solid white;
  border-radius: 7px;
}

.hirebtn:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  cursor: pointer;
}

.hirebtn:active {
  -webkit-transform: scale(1.099);
      -ms-transform: scale(1.099);
          transform: scale(1.099);
}

#exitbtn {
  padding: 13px;
  border: 0px solid white;
  border-radius: 6px;
  -webkit-transform: scale(1.135);
      -ms-transform: scale(1.135);
          transform: scale(1.135);
  background-color: rgb(252, 234, 234);
  color: red;
}

#exitbtn:hover {
  cursor: pointer;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-name: colorswitch;
          animation-name: colorswitch;
  background-color: rgb(196, 23, 23);
  color: white;
}

@-webkit-keyframes colorswitch {
  from {
    background-color: rgb(252, 234, 234);
    color: red;
  }

  to {
    background-color: rgb(196, 23, 23);
    color: white;
  }
}

@keyframes colorswitch {
  from {
    background-color: rgb(252, 234, 234);
    color: red;
  }

  to {
    background-color: rgb(196, 23, 23);
    color: white;
  }
}

.modal-content {
  color: black;
}



